import React, { useState } from 'react';
import { HealthData } from '../types/HealthData';
import { Button, TextField, Box } from '@mui/material';

interface DataFormProps {
  onAddData: (data: HealthData) => void;
}

export const DataForm: React.FC<DataFormProps> = ({ onAddData }) => {
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [weight, setWeight] = useState('');
  const [bodyFatPercentage, setBodyFatPercentage] = useState('');
  const [skeletalMusclePercentage, setSkeletalMusclePercentage] = useState('');
  const [basalMetabolism, setBasalMetabolism] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (weight && bodyFatPercentage && skeletalMusclePercentage && basalMetabolism) {
      onAddData({
        date,
        weight: parseFloat(weight),
        bodyFatPercentage: parseFloat(bodyFatPercentage),
        skeletalMusclePercentage: parseFloat(skeletalMusclePercentage),
        basalMetabolism: parseInt(basalMetabolism),
      });
      setWeight('');
      setBodyFatPercentage('');
      setSkeletalMusclePercentage('');
      setBasalMetabolism('');
    }
  };

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ mt: 2 }}>
      <TextField
        label=""
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
        fullWidth
        margin="normal"
        InputLabelProps={{
          shrink: true,
        }}
      />
      <TextField
        label=" (kg)"
        type="number"
        value={weight}
        onChange={(e) => setWeight(e.target.value)}
        fullWidth
        margin="normal"
      />
      <TextField
        label=" (%)"
        type="number"
        value={bodyFatPercentage}
        onChange={(e) => setBodyFatPercentage(e.target.value)}
        fullWidth
        margin="normal"
      />
      <TextField
        label=" (%)"
        type="number"
        value={skeletalMusclePercentage}
        onChange={(e) => setSkeletalMusclePercentage(e.target.value)}
        fullWidth
        margin="normal"
      />
      <TextField
        label=" (kcal)"
        type="number"
        value={basalMetabolism}
        onChange={(e) => setBasalMetabolism(e.target.value)}
        fullWidth
        margin="normal"
      />
      <Button type="submit" variant="contained" color="primary" fullWidth>
        
      </Button>
    </Box>
  );
};
